<template>
  <div :class="{'aboutUs':true, 'aboutUsHidden': isShowAboutUsOfWidth && isShowAboutUsOfRouter}" 
  v-if="isShowAboutUsOfWidth && isShowAboutUsOfRouter">
      <nav-bar :noMask="false"></nav-bar>
      <mask-list></mask-list>
      <div :class="{'aboutUsOfMobile':true,'bannerTop':!(isiOS||isAndroid)}">
        <div class="defect">
          <div class="defectImgWarp"></div>
          <p class="defectTitle">页面丢失</p>  
          <span class="defecttext">页面错误，相关论文暂时只能在PC端上访问</span>
          <div class="defectBtn" @click="handleClickGoHome">
            返回首页
          </div>
        </div>
      </div>
  </div>

   <div class="aboutUs" v-else>
    <nav-bar></nav-bar>
    <mask-list></mask-list>
    <div :class="{'banner':true,'bannerTop':!(isiOS||isAndroid)}">
      <h1>{{$t('aboutLanguge.bannerTitle')}}</h1>
    </div>
    <div class="main">
      <div class="container">
        <div class="row">
            <div class="nav col-xs-12 col-md-2 ">
              <div class="nullDom hidden-xs hidden-sm"></div>
              <ul class="nav-list">
                <router-link to="/aboutUs/laboratory" tag="li" active-class="active-li">{{$t('aboutLanguge.sideNavBarTitle[0]')}}</router-link>
                <router-link to="/aboutUs/thesis" tag="li" active-class="active-li" class="hidden-xs hidden-sm">{{$t('aboutLanguge.sideNavBarTitle[1]')}}</router-link>
                <router-link to="/aboutUs/joinUs" tag="li" active-class="active-li">{{$t('aboutLanguge.sideNavBarTitle[2]')}}</router-link>
              </ul>
            </div>
            <div class="content col-xs-12 col-md-10">
              <router-view></router-view>
            </div>
        </div>
      </div>
    </div>
    <foot-bar></foot-bar>
   </div>
</template>

<script>
import { mapState } from 'vuex';
import NavBar from "../../components/NavBar.vue";
import MaskList from '../../components/mask';
import footBar from "../../components/foot";
export default {
  components: {
    NavBar,
    MaskList,
    footBar
  },
  data() {
    return {
      fullWidth: document.documentElement.clientWidth,
      isShowAboutUsOfWidth: false,
      isShowAboutUsOfRouter: false,
    }
  },
  created() {
    window.addEventListener('resize', this.handleRisize);
  },
  mounted() {
    this.$nextTick(() => {
      const aboutUs = document.querySelector('.aboutUs');
      aboutUs.style.height = document.documentElement.clientHeight + 'px';
    })
    if(this.$router.currentRoute.path !== '/aboutUs/thesis') {
      const val = this.isMask
      const indexH = document.querySelector('.aboutUs');
      const mainH = document.documentElement.clientHeight;
      this.$nextTick(() => {
        indexH.style.height =val? mainH +'px' : 100 +'%';
        indexH.style.overflow = val? 'hidden' : 'visible';
      })
    }
    this.handleRisize();
  },
  beforeRouteEnter(to,from,next){
    next(vm => {
      if(to.path === '/aboutUs/thesis') {
        vm.isShowAboutUsOfRouter = true
      }else {
        vm.isShowAboutUsOfRouter = false
      }
    })
  },
  computed: {
    ...mapState({
      isMask: state => state.isMask,
      isAndroid: state => state.isAndroid,
      isiOS: state => state.isiOS
    })
  },
  watch: {
    isMask: function(val) {
      const indexH = document.querySelector('.aboutUs');
      const mainH = document.documentElement.clientHeight;
      this.$nextTick(() => {
        indexH.style.height =val? mainH +'px' : 100 +'%';
        indexH.style.overflow = val? 'hidden' : 'visible';
      })
    },
    $route: {
      handler(val){
        if(val.path === '/aboutUs/thesis') {
          this.isShowAboutUsOfRouter = true
        }else {
          this.isShowAboutUsOfRouter = false
        }
      },
    }
  },
  methods: {
    handleClickGoHome() {
      this.$router.push({
        path:'/index'
      })
    },
    handleRisize() {
      this.fullWidth = document.documentElement.clientWidth
      if(this.fullWidth<992) {
        this.isShowAboutUsOfWidth = true
      }else {
        this.isShowAboutUsOfWidth = false
      }
    }
  }
}
</script>

